html {
  width: 100vw;
  height: 100vh;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  --clr-bg: #fafafa12;
  --clr-bg2: #18181b;
  --clr-bg3: #27272a;
  --clr-primary: cadetblue;
}

body {
  width: 100%;
  height: 100%;
}

html, body {
  margin: 0;
  padding: 0;
  background-color:hsl(240 10% 4%);
  background-image: radial-gradient(var(--clr-bg) 1px, transparent 0);
  background-size: 16px 16px;
}

input {
  color: white;
  font-size: 1rem;
  background-color: transparent;
  border: none;

  &:focus-visible {
    border: none;
    outline: none;
  }

  &::placeholder {
    color: var(--clr-bg3);
  }
}

.iconButton {
  border: none;
  background-color: var(--clr-bg2);
  color: white;
  font-size: 1rem;

  :hover {
    color: var(--clr-primary);
  }
}
